import { Component, OnInit } from '@angular/core';
import { BeerService } from 'src/app/shared/service/beer.service';
import { Beer } from 'src/app/shared/model/beer.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-live-list',
  templateUrl: './live-list.component.html',
  styleUrls: ['./live-list.component.css']
})
export class LiveListComponent implements OnInit {

  beers: Beer[];
  public beerForm: FormGroup;

  constructor(public beerService: BeerService, private fb: FormBuilder) { }

  ngOnInit(): void {

    this.getBeers();

    this.beerForm = this.fb.group({
      name: ['',[Validators.required]]
    });
  }

  getBeersByType(){
    this.beerService.getBeersByType(this.beerForm.value).subscribe(
      data => {
        
        this.beers = data;
        console.log(this.beers[0]);
      }
    )
  }

  getBeers(){
    this.beerService.getBeers().subscribe(
      data => {
        
        this.beers = data;
        console.log(this.beers[0]);
      }
    )
  }

  

}
