import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ResponsePageable } from '../model/responsePageable.model';
import { Beer } from '../model/beer.model';

@Injectable({
  providedIn: 'root'
})
export class BeerService {

  apiUrl = "http://localhost:8080/beer/";

  httpOptions = {
    headres: new HttpHeaders({
      'Contet-Type':'application/json'
    })
  }

  constructor(private httpClient: HttpClient) { }

  public getBeersByType(beer: any): Observable<Beer[]>{
    return this.httpClient.get<Beer[]>(this.apiUrl + beer.name);
  }

  public getBeers(): Observable<Beer[]>{
    return this.httpClient.get<Beer[]>(this.apiUrl + "ipa");
  }


}
